<template>
	<view class="container">
		<!-- 顶部搜索栏 -->
		<view class="search-bar">
			<uni-icons type="search" size="14" color="#999"/>
			<input 
				type="text" 
				v-model="searchKeyword"
				placeholder="搜索闲置物品" 
				placeholder-class="search-placeholder"
				@confirm="onSearch"
			/>
		</view>

		<!-- 瀑布流内容区 -->
		<view class="waterfall">
			<view class="waterfall-left">
				<view 
					v-for="item in leftList" 
					:key="item._id" 
					class="card" 
					@tap="navigateToDetail(item._id)"
				>
					<image 
						class="card-image" 
						:src="item.images[0] || '/static/images/content/default.jpg'" 
						mode="widthFix"
					/>
					<view class="card-content">
						<text class="title">{{item.title}}</text>
						<text class="desc">{{item.description}}</text>
						<text class="price" v-if="item.price">¥{{item.price}}</text>
						<view class="user-info">
							<image 
								class="avatar" 
								:src="item.userInfo.avatar || '/static/images/avatar/default.png'" 
								mode="aspectFill"
							/>
							<text class="nickname">{{item.userInfo.nickname}}</text>
							<view class="action-btns">
								<button class="btn chat" @tap.stop="chatWithSeller(item)">私聊</button>
								<button class="btn buy" @tap.stop="buyItem(item)">购买</button>
							</view>
						</view>
					</view>
				</view>
			</view>
			<view class="waterfall-right">
				<view 
					v-for="item in rightList" 
					:key="item._id" 
					class="card" 
					@tap="navigateToDetail(item._id)"
				>
					<image 
						class="card-image" 
						:src="item.images[0] || '/static/images/content/default.jpg'" 
						mode="widthFix"
					/>
					<view class="card-content">
						<text class="title">{{item.title}}</text>
						<text class="desc">{{item.description}}</text>
						<text class="price" v-if="item.price">¥{{item.price}}</text>
						<view class="user-info">
							<image 
								class="avatar" 
								:src="item.userInfo.avatar || '/static/images/avatar/default.png'" 
								mode="aspectFill"
							/>
							<text class="nickname">{{item.userInfo.nickname}}</text>
							<view class="action-btns">
								<button class="btn chat" @tap.stop="chatWithSeller(item)">私聊</button>
								<button class="btn buy" @tap.stop="buyItem(item)">购买</button>
							</view>
						</view>
					</view>
				</view>
			</view>
		</view>

		<!-- 加载更多提示 -->
		<view class="loading-state" v-if="contentList.length > 0">
			<text v-if="loading">加载中...</text>
			<text v-else-if="hasMore">上拉加载更多</text>
			<text v-else>没有更多了</text>
		</view>
	</view>
</template>

<script>
export default {
	data() {
		return {
			searchKeyword: '',
			contentList: [],
			loading: false,
			hasMore: true,
			hasAuth: false,
			currentSchool: '',
			pagination: {
				page: 1,
				pageSize: 10,
				total: 0,
				totalPage: 1
			}
		}
	},

	computed: {
		leftList() {
			return this.contentList.filter((_, index) => index % 2 === 0);
		},
		rightList() {
			return this.contentList.filter((_, index) => index % 2 === 1);
		}
	},

	onShow() {
		this.checkAuth();
	},

	// 下拉刷新
	onPullDownRefresh() {
		this.loadContentList(true);
	},

	// 触底加载更多
	onReachBottom() {
		if (!this.loading && this.pagination.page < this.pagination.totalPage) {
			this.loadMore();
		}
	},

	methods: {
		// 检查认证状态
		async checkAuth() {
			try {
				const userId = uni.getStorageSync('userId');
				if (!userId) {
					this.hasAuth = false;
					return;
				}

				const userInfo = uni.getStorageSync('userInfo');
				if (userInfo?.educationInfo?.verified) {
					this.hasAuth = true;
					this.currentSchool = userInfo.educationInfo.schoolName;
					this.loadContentList(true);
				} else {
					this.hasAuth = false;
				}
			} catch (error) {
				console.error('检查认证状态失败:', error);
			}
		},

		// 加载内容列表
		async loadContentList(reset = false) {
			if (reset) {
				this.pagination.page = 1;
				this.contentList = [];
			}

			if (this.loading || !this.hasAuth) return;

			this.loading = true;
			try {
				const res = await uniCloud.callFunction({
					name: 'getContentList',
					data: {
						collection: 'idles',
						keyword: this.searchKeyword,
						userId: uni.getStorageSync('userId'),
						page: this.pagination.page,
						pageSize: this.pagination.pageSize
					}
				});

				if (res.result.code === 0) {
					const { list, pagination } = res.result.data;
					if (reset) {
						this.contentList = list;
					} else {
						this.contentList = [...this.contentList, ...list];
					}
					this.pagination = pagination;
				} else {
					throw new Error(res.result.msg);
				}
			} catch (error) {
				console.error('获取内容列表失败:', error);
				uni.showToast({
					title: error.message || '获取数据失败',
					icon: 'none'
				});
			} finally {
				this.loading = false;
				uni.stopPullDownRefresh();
			}
		},

		// 搜索
		onSearch() {
			if (!this.searchKeyword.trim()) return;
			this.loadContentList(true);
		},

		// 加载更多
		loadMore() {
			if (this.pagination.page >= this.pagination.totalPage) return;
			this.pagination.page++;
			this.loadContentList();
		},

		// 跳转到详情页
		navigateToDetail(id) {
			uni.navigateTo({
				url: `/pages/idle/detail?id=${id}`
			});
		},

		// 私聊卖家
		chatWithSeller(item) {
			if (!this.hasAuth) {
				uni.showToast({
					title: '请先登录',
					icon: 'none'
				});
				return;
			}
			uni.showToast({
				title: '私聊功能开发中',
				icon: 'none'
			});
		},

		// 购买商品
		buyItem(item) {
			if (!this.hasAuth) {
				uni.showToast({
					title: '请先登录',
					icon: 'none'
				});
				return;
			}
			uni.showToast({
				title: '购买功能开发中',
				icon: 'none'
			});
		}
	}
}
</script>

<style>
.container {
	padding: 0 20rpx;
	background-color: #ffffff;
	min-height: 100vh;
}

.search-bar {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	height: 88rpx;
	padding: 0 30rpx;
	background: rgba(255, 255, 255, 0.95);
	display: flex;
	align-items: center;
	z-index: 99;
	box-shadow: 0 2rpx 10rpx rgba(0, 0, 0, 0.1);
}

.search-bar input {
	flex: 1;
	margin-left: 10rpx;
	font-size: 28rpx;
}

.waterfall {
	display: flex;
	justify-content: space-between;
	padding-top: 100rpx;
	margin: 0 10rpx;
}

.waterfall-left, .waterfall-right {
	width: 48%;
	display: flex;
	flex-direction: column;
	gap: 20rpx;
}

.waterfall-left {
	margin-top: -20rpx; /* 左侧列向上偏移 */
}

.card {
	background: #fff;
	border-radius: 12rpx;
	overflow: hidden;
	box-shadow: 0 2rpx 10rpx rgba(0, 0, 0, 0.1);
	transition: all 0.3s;
}

.card:hover {
	transform: translateY(-2rpx);
	box-shadow: 0 4rpx 12rpx rgba(0, 0, 0, 0.15);
}

.card-image {
	width: 100%;
	border-radius: 12rpx 12rpx 0 0;
}

.card-content {
	padding: 16rpx;
}

.title {
	font-size: 28rpx;
	color: #333;
	font-weight: bold;
	line-height: 1.4;
	margin-bottom: 8rpx;
}

.desc {
	font-size: 24rpx;
	color: #666;
	line-height: 1.4;
	display: -webkit-box;
	-webkit-box-orient: vertical;
	-webkit-line-clamp: 2;
	overflow: hidden;
	margin-bottom: 8rpx;
}

.price {
	font-size: 32rpx;
	color: #ff5757;
	font-weight: bold;
	margin: 8rpx 0;
}

.user-info {
	display: flex;
	align-items: center;
	padding-top: 12rpx;
	border-top: 1rpx solid #f5f5f5;
	margin-top: 12rpx;
}

.avatar {
	width: 44rpx;
	height: 44rpx;
	border-radius: 50%;
	border: 2rpx solid #fff;
	box-shadow: 0 2rpx 4rpx rgba(0, 0, 0, 0.1);
}

.nickname {
	font-size: 24rpx;
	color: #666;
	margin-left: 8rpx;
	flex: 1;
}

.action-btns {
	display: flex;
	gap: 10rpx;
}

.btn {
	padding: 6rpx 16rpx;
	border-radius: 24rpx;
	font-size: 24rpx;
	border: none;
}

.btn.chat {
	background-color: #fff;
	border: 1rpx solid #ff5757;
	color: #ff5757;
}

.btn.buy {
	background-color: #ff5757;
	color: #fff;
}

.loading-state {
	text-align: center;
	padding: 20rpx 0;
	color: #999;
	font-size: 24rpx;
}
</style>